import React from 'react';
import {InternalLink} from '../framework/Links';
import {RouteID} from "../application/Routes";
import './DigitalizeContent.scss';

export default class DigitalizeContent extends React.PureComponent {
  render() {
    return (
      <main id='digitalize'>
          <section>
              <h5>THE STORY OF DIGITALIZATION</h5>
              <p>
                  A few years ago <strong>Ultima Labs</strong> team have been engaged to assist with the digitalization of the paper-based process
                  for a client in Queensland.
              </p>
              <p>
                  The client sought to improve existing paper-based process by making it electronic-first.
                  Inherently, the paper-based process was slow and was prone to the various human factors: data was inconsistent,
                  was not preventing the invalid input and it was difficult to reconcile data.
              </p>
              <p>
                  During the journey of the digitalization our team gained a necessary expertise in the subject and suggested various solutions.
                  By applying agile methodologies for software development, the solutions were processed in multiple iterations, allowing
                  fast suitability assessments and quick adjustments on the fly with no compromise on quality.
              </p>
              <p>
                  To ease the adoption of the system and onboarding, the existing paper-based data have been digitized.
                  The digitization helped to identify various edge cases that can happen in real life, which were considered
                  in the final implementation as well.
              </p>
          </section>
          <section className='blockquote'>
              <blockquote>
                  <div>

                  </div>
              </blockquote>
          </section>
          <section className='cta'>
              <InternalLink routeID={RouteID.ROUTE_ID_CONTACT} type='primary accent button'>Contact us</InternalLink>
          </section>
      </main>
    );
  }
}