import React from 'react';
import './ModernizeContent.scss';
import {ExternalLink, InternalLink, LinkTarget} from '../framework/Links';
import {RouteID} from "../application/Routes";
import photo from "../assets/jh.jpg";


export default class ModernizeContent extends React.PureComponent {
  render() {
    return (
      <main id='modernize'>
          <section>
              <h5>THE STORY OF MODERNIZATION</h5>
              <p>
                  <strong>Ultima Labs</strong> team have been engaged by <strong>MPower MSL</strong> to assess
                  modernization opportunities of the Vantage software they acquired with <strong>Verteda</strong> company.
              </p>
              <p>
                  Vantage is the workforce management software developed by Verteda with the first version developed a few decades ago.
                  The Vantage platform nowadays has rich and comprehensive feature set, making it a strong competitor on the market,
                  however as there was no need for active development, its user interface may appear outdated.
                  MPower MSL sought for the solutions to make the user interface more appealing and modern-looking.
              </p>
              <p>
                  The task of modernization was complicated by two additional factors.
                  Although Ultima Labs team had both source code and running application, there was no way for to launch it in debug mode,
                  so it could not be analyzed step-by-step. Our team had to find and analyze behaviors in a million lines of C++ code.
                  A second factor is that our team could not build the software from the source code, as there was no way to get a suitable license for the compiler.
              </p>
              <p>
                  After a two-weeks analysis and discussion with the client our team have suggested a solution based on
                  the additional back-end that would be hosted side-by-side and capable to communicate with the existing software,
                  and offered a REST API for the front-end to use.
                  As the proof of concept, a web-based front-end was offered using Angular.
                  The solution supported secure access via existing authentication and authorization protocols,
                  a two-way communication in real time, delivering concurrent updates for boh existing and modern UIs.
                  To put it simply: any changes made by one user in the existing system would be immediately reflected
                  in the modern interface for another user and vice versa, changes made in the modern interface would be
                  immediately reflected in the legacy system.
              </p>
              <p>
                  This journey of the modernization proved high technical skills and professionalism that Utlima Labs team possesses.
              </p>
          </section>
          <section>
              <p>Find out more about:</p>
              <p><ExternalLink target={LinkTarget.Blank} href='https://mpowermsl.com/'>MPower MSL</ExternalLink></p>
              <p><ExternalLink target={LinkTarget.Blank} href='https://www.verteda.com/'>Verteda, an MSL company</ExternalLink></p>
              <p><ExternalLink target={LinkTarget.Blank} href='https://www.verteda.com/solutions/workforce-management'>Vantage - Workforce Management</ExternalLink></p>
          </section>
      </main>
    );
  }
}