import React from 'react';
import './DigitalizeSlide.scss';
import {InternalLink} from "../framework/Links";
import {RouteID} from "../application/Routes";

export default class DigitalizeSlide extends React.PureComponent {
  render() {
    return (
        <section id="digitalize-slide">
            <h1>
                WE DIGITALIZE
            </h1>
            <p>
                Digitalization helps speed up your business and free up your human resources, boost throughput and streamline your operations.
            </p>
            <p>
                At Ultima Labs we have a necessary expertise to provide you with quality digitalization solutions that are
                tailored specifically for your business needs.
            </p>
            <p>
                <InternalLink routeID={RouteID.ROUTE_ID_DIGITALIZE}>READ THE STORY</InternalLink>
            </p>
            <div>
                <InternalLink routeID={RouteID.ROUTE_ID_CONTACT} type="primary accent button" />
            </div>
      </section>
    );
  }
}