import React from 'react';
import './InnovateSlide.scss';
import {InternalLink} from "../framework/Links";
import {RouteID} from "../application/Routes";

export default class InnovateSlide extends React.PureComponent {
  render() {
    return (
        <section id="innovate-slide">
            <h1>
                WE INNOVATE
            </h1>
            <p>
                Innovation is inevitable aspect for every business in order to lead the world for the brighter future.
            </p>
            <p>
                We are here to help you bring your innovation ideas into life, make them a new reality.
            </p>
            <p>
                <InternalLink routeID={RouteID.ROUTE_ID_INNOVATE}>READ THE STORY</InternalLink>
            </p>
            <div>
                <InternalLink routeID={RouteID.ROUTE_ID_CONTACT} type="primary accent button" />
            </div>
        </section>
    );
  }
}