import React from 'react';
import './TermsContent.scss';
import {ExternalLink, InternalLink, LinkTarget} from '../framework/Links';
import {RouteID} from "../application/Routes";


export default class PrivacyContent extends React.PureComponent {
  render() {
    return (
      <main id='transform'>
          <section>
              <h5>PRIVACY POLICY</h5>
              <h6>OVERVIEW</h6>
              <p>
                  This Privacy Policy describes how your personal information is collected, used, and shared when you visit <InternalLink routeID={RouteID.ROUTE_ID_HOME}>https://ultima-labs.com.au</InternalLink> (the "Site").
              </p>
              <h6>PERSONAL INFORMATION WE COLLECT</h6>
              <p>
                  When you visit the Site, we automatically collect certain information about your device, including information about your web browser,
                  IP address, time zone, and some of the cookies that are installed on your device.
                  Additionally, as you browse the Site, we collect information about the individual web pages or products that you view,
                  what websites or search terms referred you to the Site, and information about how you interact with the Site.
                  We refer to this automatically-collected information as "Device Information".
              </p>
              <p>
                  We collect Device Information using the following technologies:
                  <ul>
                      <li>"Cookies" are data files that are placed on your device or computer and often include an anonymous unique identifier. For more information about cookies, and how to disable cookies, visit <ExternalLink href="http://www.allaboutcookies.org" target={LinkTarget.Blank}/>.</li>
                      <li>"Log files" track actions occurring on the Site, and collect data including your IP address, browser type, Internet service provider, referring/exit pages, and date/time stamps.</li>
                      <li>"Web beacons", "tags", and "pixels" are electronic files used to record information about how you browse the Site.</li>
                  </ul>
              </p>
              <p>
                  Additionally when you make a contact through the Site, we collect certain information from you, including your name, email address, and phone number.
                  We refer to this information as "Contact Information".
              </p>
              <p>
                  When we talk about "Personal Information" in this Privacy Policy, we are talking both about Device Information and Contact Information.
              </p>
              <h6>HOW DO WE USE YOUR PERSONAL INFORMATION?</h6>
              <p>
                  We use the Contact Information that we collect generally to communicate with you back.
              </p>
              <p>
                  We use the Device Information that we collect to help us screen for potential risk and fraud (in particular, your IP address),
                  and more generally to improve and optimize our Site (for example, by generating analytics about how our customers browse and interact with the Site,
                  and to assess the success of our marketing and advertising campaigns).
              </p>
              <h6>SHARING YOUR PERSONAL INFORMATION</h6>
              <p>
                  We share your Personal Information with third parties to help us use your Personal Information, as described above.
                  For example, we use Google reCAPTCHA to secure our online contact form, and Google Analytics and Facebook Pixel
                  to gather analytics about how our customers interact with the Site. You can read more about how Google and Facebook
                  use your Personal Information here:
                  <ul>
                      <li><ExternalLink href="https://www.google.com/intl/en/policies/privacy/" target={LinkTarget.Blank}>https://www.google.com/intl/en/policies/privacy/</ExternalLink></li>
                      <li><ExternalLink href="https://www.facebook.com/legal/FB_Work_Privacy" target={LinkTarget.Blank}>https://www.facebook.com/legal/FB_Work_Privacy</ExternalLink></li>
                  </ul>
              </p>
              <p>
                  Finally, we may also share your Personal Information to comply with applicable laws and regulations, to respond to a subpoena, search warrant or other lawful request for information we receive, or to otherwise protect our rights.
              </p>
              <h6>BEHAVIOURAL ADVERTISING</h6>
              <p>
                  As described above, we use your Personal Information to provide you with targeted advertisements or marketing communications
                  we believe may be of interest to you.
                  For more information about how targeted advertising works, you can visit the Network Advertising Initiatives ("NAI")
                  educational page at <ExternalLink href="http://www.networkadvertising.org/understanding-online-advertising/how-does-it-work" target={LinkTarget.Blank}>http://www.networkadvertising.org/understanding-online-advertising/how-does-it-work</ExternalLink>.
              </p>
              <p>
                  You can opt out of targeted advertising by:
                  <ul>
                      <li>Facebook - <ExternalLink href="https://www.facebook.com/settings/?tab=ads" target={LinkTarget.Blank}>https://www.facebook.com/settings/?tab=ads</ExternalLink></li>
                      <li>Google - <ExternalLink href="https://www.google.com/settings/ads/anonymous" target={LinkTarget.Blank}>https://www.google.com/settings/ads/anonymous</ExternalLink></li>
                  </ul>
              </p>
              <p>
                  Additionally, you can opt out of some of these services by visiting the Digital Advertising Alliances opt-out portal
                  at: <ExternalLink href="http://optout.aboutads.info" target={LinkTarget.Blank}>http://optout.aboutads.info</ExternalLink>.
              </p>
              <h6>DO NOT TRACK</h6>
              <p>
                  Please note that we do not alter our Sites data collection and use practices when we see a Do Not Track signal from your browser.
              </p>
              <h6>YOUR RIGHTS</h6>
              <p>
                  If you are a European resident, you have the right to access personal information we hold about you and to ask that your personal information be corrected, updated, or deleted. If you would like to exercise this right, please contact us through the contact information below.
              </p>
              <p>
                  Additionally, if you are a European resident we note that we are processing your information in order to fulfill contracts we might have with you, or otherwise to pursue our legitimate business interests listed above.
                  Additionally, please note that your information will be transferred outside of Europe, including to Canada and the United States.
              </p>
              <h6>CHANGES</h6>
              <p>
                  We may update this privacy policy from time to time in order to reflect, for example, changes to our practices or for other operational, legal or regulatory reasons.
              </p>
              <h6>CONTACT US</h6>
              <p>
                  For more information about our privacy practices, if you have questions, or if you would like to make a complaint, please contact
                  us using online contact form or any other method available on <InternalLink routeID={RouteID.ROUTE_ID_CONTACT} /> page.
              </p>
              <p>&nbsp;</p>
          </section>
      </main>
    );
  }
}