import React from 'react';
import { Jumbotron } from '../framework/Jumbotron';
import JumbotronSlide from '../framework/JumbotronSlide';
import DigitalizeSlide from "./DigitalizeSlide";
import ModernizeSlide from "./ModernizeSlide";
import TransformSlide from "./TransformSlide";
import InnovateSlide from "./InnovateSlide";
import ContactSlide from "./ContactSlide";
import digitalize from '../assets/0-digitalize.jpg'
import modernize from '../assets/1-modernize.jpg'
import transform from '../assets/2-transform.jpg'
import innovate from '../assets/3-innovate.jpg'
import contact from '../assets/4-contact.jpg'
import './HomeContent.scss';

export default class HomeContent extends React.PureComponent {
    componentDidMount(): void {
        let images = [
            digitalize,
            modernize,
            transform,
            innovate,
            contact
        ];

        const links = document.getElementsByTagName("link");
        for (let i = 0, length = links.length; i < length; ++i) {
            const link = links.item(i);
            if (link) {
                images = images.filter(x => !link.href.endsWith(x));
            }
        }

        if (images) {
            images.forEach(x => {
                const link = document.createElement("link");
                link.href = x;
                link.rel = "preload";
                link.as = "image";

                document.head.appendChild(link);
            })
        }
    }

  render() {
    return (
      <main>
        <section className="section-home">
          <Jumbotron>
              <JumbotronSlide backgroundImg={digitalize}>
                  <DigitalizeSlide />
              </JumbotronSlide>
              <JumbotronSlide backgroundImg={modernize}>
                  <ModernizeSlide />
              </JumbotronSlide>
              <JumbotronSlide backgroundImg={transform}>
                  <TransformSlide />
              </JumbotronSlide>
              <JumbotronSlide backgroundImg={innovate}>
                  <InnovateSlide />
              </JumbotronSlide>
              <JumbotronSlide backgroundImg={contact}>
                  <ContactSlide />
              </JumbotronSlide>
          </Jumbotron>
        </section>
      </main>
    );
  }
}