import React from 'react';
import ContactWidget from './ContactWidget';
import SVG from '../framework/SVG';
import SecureLink, {ExternalLink, LinkTarget} from '../framework/Links';
import './ContactContent.scss';
import qr from '../qr.svg'

export default class ContactContent extends React.PureComponent {
  render() {
    return (
      <main id="about-us">
        <section id="online">
          <section id="contact">
            <h5>Contact us now</h5>
            <div>
              <ContactWidget />
            </div>
          </section>
        </section>
        <section id="conventional">
          <section id="location">
            <h5>Visit us</h5>
            <SVG icon="location" />
            <address>Level 9, 307 Queen Street<br/>Brisbane, QLD 4000<br/>Australia</address>
          </section>
          <section id="correspondence">
            <h5>Mail us</h5>
            <SVG icon="envelope" />
            <address>PO Box 100<br/>Ferny Hills DC, QLD 4055<br/>Australia</address>
          </section>
          <section id="phone">
            <h5>Call us</h5>
            <SVG icon="phone" />
            <a href="tel:+61412345678">+61 412 345 678</a>
          </section>
          <section id="social">
            <h5>Follow us</h5>
            <ExternalLink href="https://twitter.com/ultimalabs" target={LinkTarget.Blank}>
              <SVG icon="twitter" />
            </ExternalLink>
            <ExternalLink href="https://www.facebook.com/ultimalabs" target={LinkTarget.Blank}>
              <SVG icon="facebook" />
            </ExternalLink>
            <ExternalLink href="https://www.instagram.com/ultimalabs" target={LinkTarget.Blank}>
              <SVG icon="instagram" />
            </ExternalLink>
            <ExternalLink href="https://www.linkedin.com/company/ultima-labs" target={LinkTarget.Blank}>
              <SVG icon="linkedin" />
            </ExternalLink>
            <ExternalLink href="https://github.com/ultima-labs" target={LinkTarget.Blank}>
              <SVG icon="github" />
            </ExternalLink>
          </section>
          <section id="vcard">
            <h5>Add to your contacts</h5>
            <SVG icon="profile" />
            <SecureLink href={'vcard.vcf'} isDownload={true}>Download a VCARD</SecureLink>
          </section>
        </section>
        <section id="qr">
          <h5>Add to your phone</h5>
          <img src={qr} alt="Contact QR code" />
        </section>
      </main>
    );
  }
}