import React from 'react';
import { nameof } from '../framework/Common';
import { GoogleCaptcha } from '../framework/GoogleCaptcha';
import logo from '../logo.svg';
import './ContactWidget.scss';

declare global {
  function gtag(...params: any[]): void;
}

export type ContactWidgetFieldsState = {
  name: string;
  email: string;
  phone: string;
  subject: string;
  message: string;
  token: string;
};

export type ContactWidgetProps = {
}

export type ContactWidgetState = {
  values: ContactWidgetFieldsState;
  errors: ContactWidgetFieldsState;
  status: 'ready' | 'sending' | 'sent' | 'error';
}

export default class ContactWidget extends React.Component<ContactWidgetProps, ContactWidgetState> {
  baseState: ContactWidgetState;

  constructor(props: ContactWidgetProps) {
    super(props);

    this.state = {
      values: { 
        name: "",
        email: "",
        phone: "",
        subject: "",
        message: "",
        token: ""
      },
      errors: { 
        name: "",
        email: "",
        phone: "",
        subject: "",
        message: "",
        token: ""
      },
      status: 'ready'
    }

    this.baseState = this.state;
    this.onToken = this.onToken.bind(this);
    this.onChange = this.onChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
    this.onReset = this.onReset.bind(this);
  }

  onToken(token: string) {
    this.setValue(nameof<ContactWidgetFieldsState>("token"), token);
  }

  onChange(event: React.ChangeEvent<HTMLInputElement> | React.ChangeEvent<HTMLTextAreaElement>) {
    const name = event.target.name as keyof ContactWidgetFieldsState;
    const value = event.target.value;
    this.setValue(name, value);
  }

  async onSubmit(event: React.ChangeEvent<HTMLFormElement>) {
    this.setState({ status: 'sending' });

    try {
      event.preventDefault();

      let hasErrors = Object.values(this.state.errors).filter(x => x.length > 0).length > 0;

      if (!this.state.values.token) {
        hasErrors = true;
        this.setError(nameof<ContactWidgetFieldsState>("token"), "Please also check \"I'm not a robot\" above");
      }

      if (!this.state.values.name) {
        hasErrors = true;
        this.setError(nameof<ContactWidgetFieldsState>("name"), "Please tell us your name so that we know how to greet you");
      }

      if (!this.state.values.email) {
        hasErrors = true;
        this.setError(nameof<ContactWidgetFieldsState>("email"), "Please tell us your email so that we know how to contact you");
      }

      if (!this.state.values.message) {
        hasErrors = true;
        this.setError(nameof<ContactWidgetFieldsState>("message"), "Just a brief message how we can help you");
      }

      if (hasErrors) {
        gtag('event', 'validation failure', {
          'event_category': 'contact',
          'event_label': 'failure',
          'value': 'value'
        });
        
        this.setState({ status: 'ready' });
        return;
      }

      const submissionStart = window.performance ? window.performance.now() : 0;

      const url = "https://ultima-labs.azure-api.net/ultima-labs.com.au/contact/";           
      const options: RequestInit = {
        method: "POST",
        body: this.state.values.message,
        headers: {
          "Content-Type": "text/plain",
          "X-From-Name": this.state.values.name,
          "X-From-Email": this.state.values.email,
          "X-From-Phone": this.state.values.phone,
          "X-Subject": this.state.values.subject
        }
      };

      await fetch(url, options);

      const submissionEnd = window.performance ? window.performance.now() : 0;

      setInterval(this.onReset, 5000);

      this.setState({ status: 'sent' });

      if (window.performance) {
        gtag('event', 'timing_complete', {
          'name' : 'Send e-mail',
          'value' : submissionEnd - submissionStart,
          'event_category' : 'contact'
        });
      }
      
      gtag('event', 'submission success', {
        'event_category': 'contact',
        'event_label': 'success',
        'value': 'value'
      });
    } catch (e) {
      console.error(e);

      gtag('event', 'submission error', {
        'event_category': 'contact',
        'event_label': 'failure',
        'value': e.toString()
      });

      setInterval(this.onReset, 5000);

      this.setState({ status: 'error' });
    }
  }

  onReset() {
    this.setState(this.baseState);
  }

  setValue(name: keyof ContactWidgetFieldsState, value: string) {
    this.setState((x: ContactWidgetState) => {
      return {
        ...x,
        values: {
          ...x.values,
          [name]: value,
        },
        errors: {
          ...x.errors,
          [name]: ""
        }
      }
    });
  }

  setError(name: keyof ContactWidgetFieldsState, error: string) {
    this.setState((x: ContactWidgetState) => {
      return {
        ...x,
        errors: {
          ...x.errors,
          [name]: error
        }
      }
    });
  }

  render() {
    const viewForm = 
      <form onSubmit={this.onSubmit}>
        <div>
          <label className="required">Your name</label>
          <input type="text" name={nameof<ContactWidgetFieldsState>("name")}
                value={this.state.values.name} 
                disabled={this.state.status !== 'ready'}
                onChange={this.onChange} />
          <span className="form-error">{this.state.errors.name}</span>
        </div>
        <div>
          <label className="required">Your email</label>
          <input type="email" name={nameof<ContactWidgetFieldsState>("email")}
                value={this.state.values.email} 
                disabled={this.state.status !== 'ready'}
                onChange={this.onChange} />
          <span className="form-error">{this.state.errors.email}</span>
        </div>
        <div>
          <label>Your phone</label>
          <input type="telephone" name={nameof<ContactWidgetFieldsState>("phone")}
                value={this.state.values.phone} 
                disabled={this.state.status !== 'ready'}
                onChange={this.onChange} />
          <span className="form-error">{this.state.errors.phone}</span>
        </div>
        <div>
          <label>Subject</label>
          <input type="text" name={nameof<ContactWidgetFieldsState>("subject")}
                value={this.state.values.subject} 
                disabled={this.state.status !== 'ready'}
                onChange={this.onChange} />
          <span className="form-error">{this.state.errors.subject}</span>
        </div>
        <div>
          <label className="required">Message</label>
          <textarea name={nameof<ContactWidgetFieldsState>("message")}
                    value={this.state.values.message} 
                    disabled={this.state.status !== 'ready'}
                    onChange={this.onChange} />
          <span className="form-error">{this.state.errors.message}</span>
        </div>
        <div>
          <label className="required-description">Fields marked with asterisks are required.</label>
          <label>Personal details collected are used solely to reply you back and will not be stored or passed further to any third parties.</label>
          <GoogleCaptcha onValidated={this.onToken} />
          <input type="submit" value="Send" className="primary accent" 
                disabled={this.state.status !== 'ready'} />
          <span className="form-error">{this.state.errors.token}</span>
        </div>
      </form>;

    const viewSent = 
      <div id="contact-widget-sent">
        <img src={logo} alt="Ultima Labs logo"></img>
        <p>Thank you for contacting us!<br/>Your message was sent successfully.</p>
        <p>We will be in touch shortly.</p>
      </div>;

    const viewError = 
      <div id="contact-widget-error">
        <p>Something went wrong...<br/>Your message was not sent.</p>
        <p>Please try again later.</p>
      </div>;

    if (this.state.status === 'sent') {
      return viewSent;
    } else if (this.state.status === 'error') {
      return viewError;
    } else {
      return viewForm;
    }
  }
}