import React from 'react';
import './InnovateContent.scss';
import {ExternalLink, InternalLink, LinkTarget} from '../framework/Links';
import {RouteID} from "../application/Routes";
import photo from "../assets/tb.jpg";

export default class InnovateContent extends React.PureComponent {
  render() {
    return (
      <main id='innovate'>
          <section>
              <h5>THE STORY OF INNOVATION</h5>
              <p>
                  <strong>Ultima Labs</strong> team have been engaged
                  by <strong>Console Group</strong> to assist their team with the innovation in the real estate sector by introducing
                  an integrated payment solution, namely <strong>Console Pay</strong>.
              </p>
              <p>
                  The integrated payment solution became possible via comprehensive integrations between their
                  products, <strong>Console Gateway</strong> and <strong>Console Cloud</strong>, and third party service providers.
                  Nowadays Console Pay is a powerful direct debit and receipting solution that helps property managers take control of tenant payments.
              </p>
              <p>
                  In order to facilitate and speed up the adoption of the solution, Console Pay was extended with
                  invitations sub-system, allowing property managers to invite tenants for recurrent or one-off direct debits.
              </p>
              <p>
                  Due to the innovative nature of the project, there was no certain "right" way of implementing the solution,
                  so multiple approaches have been tested during the development. Early focus group engagement helped elaborate
                  the most convenient and user-friendly user experience.
              </p>
          </section>
          <section className='blockquote'>
              <blockquote>
                  <img src={photo} alt='Todd Bowles, Head of Engineering at Console Group'/>
                  <div>
                      <div>Ultima Labs is an excellent provider of quality software solutions. Their team members possess considerable technical knowledge, and the experience and skill to apply it appropriately to the situation to generate excellent outcomes.</div>
                      <div>We brought Ultima Labs on as a contractor to augment an existing team in the delivery of a piece of integration work. They embedded in the team cleanly and without friction, and the group constantly and consistently delivered high quality incremental improvements and new features.</div>
                      <div>I recommend Ultima Labs to anyone looking for a self-reliant software developers with an eye for technical detail.</div>
                      <p>&mdash; Todd Bowles, Head of Engineering at Console Group</p>
                  </div>
              </blockquote>
          </section>
          <section className='cta'>
              <InternalLink routeID={RouteID.ROUTE_ID_CONTACT} type='primary accent button'>Contact us</InternalLink>
          </section>
          <section>
              <p>Find out more about:</p>
              <p><ExternalLink target={LinkTarget.Blank} href='https://www.console.com.au/'>Console Group</ExternalLink></p>
              <p><ExternalLink target={LinkTarget.Blank} href='https://www.console.com.au/add-ons/consolepay/'>Console Pay</ExternalLink></p>
              <p><ExternalLink target={LinkTarget.Blank} href='https://www.console.com.au/products/gateway/'>Console Gateway</ExternalLink></p>
              <p><ExternalLink target={LinkTarget.Blank} href='https://www.console.com.au/products/cloud/'>Console Cloud</ExternalLink></p>
          </section>
      </main>
    );
  }
}