import React from 'react';
import './TransformContent.scss';
import {ExternalLink, InternalLink, LinkTarget} from '../framework/Links';
import {RouteID} from "../application/Routes";
import photo from "../assets/mt.jpg";


export default class TransformContent extends React.PureComponent {
  render() {
    return (
      <main id='transform'>
          <section>
              <h5>THE STORY OF TRANSFORMATION</h5>
              <p>
                  <strong>Ultima Labs</strong> team have been engaged by <strong>Sunsuper</strong> superannuation fund
                  to assist with the transformation of the existing solution for the superannuation fund members named <strong>Member Online</strong>.
              </p>
              <p>
                  Sunsuper sought to transform Member Online into a better portal for members,
                  to provide better certainty and clarity for users, so they could better understand the shape of their superannuation.
              </p>
              <p>
                  Ultima Labs team was challenged with the scale of the project and have been asked a question on the best
                  way to transform the platform in multiple steps, minimizing risks, rather than doing it in one hit and taking a one big risk at the end.
              </p>
              <p>
                  The solution was to set up the base framework which business-as-usual teams could use and follow to perform the final transition with no rush.
                  This framework included core software components to allow user a secure access via authentication and authorization, a brand-new dashboard with
                  the most important data displayed at the home page, a sample of the page that would serve as a new way of doing things.
                  To achieve feature parity, many other pages were integrated using deep links to the existing pages, with page styles overridden specifically for the portal,
                  giving the same modern and refreshed look and feel.
                  The solution was also using the new REST API our team has also created to provide relevant data for the dashboard.
              </p>
              <p>
                  In the beginning of this journey the team was facing a lot of blockers and issues to overcome,
                  in addition to the target release date pressure.
                  However all of those blockers and issues have been resolved successfully prior the release and the platform went live as expected.
              </p>
          </section>
          <section>
              <p>Find out more about:</p>
              <p><ExternalLink target={LinkTarget.Blank} href='https://www.sunsuper.com.au/'>Sunsuper</ExternalLink></p>
              <p><ExternalLink target={LinkTarget.Blank} href='https://member.secure.sunsuper.com.au/'>Member Online</ExternalLink></p>
          </section>
          {/*
          <section className='blockquote'>
              <blockquote>
                  <img src={photo} alt='Marcus Trant, Program Manager at Sunsuper'/>
                  <div>
                      You guys awesome!
                      <p>&mdash; Marcus Trant, Program Manager at Sunsuper</p>
                  </div>
              </blockquote>
          </section>
          <section className='cta'>
              <InternalLink routeID={RouteID.ROUTE_ID_CONTACT} type='primary accent button'>Contact us</InternalLink>
          </section>
          <section>
              <p>Find out more about:</p>
              <p><ExternalLink href='https://gispeople.com.au/'>GIS People</ExternalLink></p>
              <p><ExternalLink href='https://www.ecq.qld.gov.au/'>Electoral Commission Queensland</ExternalLink></p>
              <p><ExternalLink href='https://disclosures.ecq.qld.gov.au/'>Electronic Disclosure System</ExternalLink></p>
          </section>*/}
      </main>
    );
  }
}