import React from 'react';
import './TransformSlide.scss';
import {InternalLink} from "../framework/Links";
import {RouteID} from "../application/Routes";

export default class TransformSlide extends React.PureComponent {
  render() {
    return (
        <section id="transform-slide">
            <h1>
                WE TRANSFORM
            </h1>
            <p>
                As customer needs change, your product or service has to change as well.
            </p>
            <p>
                Here at Ultima Labs we embrace transformation, helping our customers transform their businesses and
                processes regardless of the complexity and with no compromise on quality.
            </p>
            <p>
                <InternalLink routeID={RouteID.ROUTE_ID_TRANSFORM}>READ THE STORY</InternalLink>
            </p>
            <div>
                <InternalLink routeID={RouteID.ROUTE_ID_CONTACT} type="primary accent button" />
            </div>
        </section>
    );
  }
}