import React from 'react';
import HomeContent from '../pages/HomeContent';
import DigitalizeContent from "../pages/DigitalizeContent";
import ModernizeContent from "../pages/ModernizeContent";
import TransformContent from "../pages/TransformContent";
import InnovateContent from "../pages/InnovateContent";
import ContactContent from '../pages/ContactContent';
import PrivacyContent from "../pages/PrivacyContent";
import TermsContent from "../pages/TermsContent";
import DummyContent from "../pages/DummyContent";

export enum RouteID {
    ROUTE_ID_HOME = 1,
    ROUTE_ID_DIGITALIZE,
    ROUTE_ID_MODERNIZE,
    ROUTE_ID_TRANSFORM,
    ROUTE_ID_INNOVATE,
    ROUTE_ID_CONTACT,
    ROUTE_ID_PRIVACY,
    ROUTE_ID_TERMS,
    ROUTE_ID_DUMMY
}

export interface RouteDef {
    path: string,
    pageTitle: string,
    linkTitle: string,
    hash?: string, /*  An anchor on the page */
    control: React.ReactElement, /* A control to render the page */
    parents?: RouteID[]
    isMainMenu?: boolean
}

const Routes: { [id: number]: RouteDef } = {
    [RouteID.ROUTE_ID_HOME]: { 
        path: '/', 
        pageTitle: 'Welcome',
        linkTitle: 'Home',
        isMainMenu: true,
        control: <HomeContent /> },
    [RouteID.ROUTE_ID_DIGITALIZE]: {
        path: '/digitalize',
        pageTitle: 'The story of digitalization',
        linkTitle: 'We digitalize',
        isMainMenu: true,
        control: <DigitalizeContent /> },
    [RouteID.ROUTE_ID_MODERNIZE]: {
        path: '/modernize',
        pageTitle: 'The story of modernization',
        linkTitle: 'We modernize',
        isMainMenu: true,
        control: <ModernizeContent /> },
    [RouteID.ROUTE_ID_TRANSFORM]: {
        path: '/transform',
        pageTitle: 'The story of transformation',
        linkTitle: 'We transform',
        isMainMenu: true,
        control: <TransformContent /> },
    [RouteID.ROUTE_ID_INNOVATE]: {
        path: '/innovate',
        pageTitle: 'The story of innovation',
        linkTitle: 'We innovate',
        isMainMenu: true,
        control: <InnovateContent /> },
    [RouteID.ROUTE_ID_CONTACT]: {
        path: '/contact', 
        pageTitle: 'Contact us',
        linkTitle: 'Contact us',
        isMainMenu: true,
        control: <ContactContent /> },
    [RouteID.ROUTE_ID_PRIVACY]: {
        path: '/privacy',
        pageTitle: 'Privacy Policy',
        linkTitle: 'Privacy Policy',
        control: <PrivacyContent /> },
    [RouteID.ROUTE_ID_TERMS]: {
        path: '/terms',
        pageTitle: 'Terms of Service',
        linkTitle: 'Terms of Service',
        control: <TermsContent /> },
    [RouteID.ROUTE_ID_DUMMY]: {
        path: '/dummy',
        pageTitle: 'Dummy',
        linkTitle: 'Dummy',
        control: <DummyContent /> }
};

export default Routes;