import React from 'react';

declare global {
    interface Window {
        grecaptcha: ReCaptchaInstance
        captchaOnLoad: () => void
    }
}
  
interface ReCaptchaInstance {
    ready: (cb: () => any) => void
    render: (id: string, options: ReCaptchaRenderOptions | string) => any
    reset: () => void;
}
  
interface ReCaptchaRenderOptions {
    sitekey: string,
    theme: "dark" | "light",
    size: "compact" | "normal"
    callback: any
}

export type GoogleReCaptchaValidatedEvent = (token: string) => void;

export type GoogleCaptchaProps = {
    onValidated: GoogleReCaptchaValidatedEvent;
}

export type GoogleCaptchaState = {
    isReady: boolean;
}

export class GoogleCaptcha extends React.Component<GoogleCaptchaProps, GoogleCaptchaState> {
    constructor(props: GoogleCaptchaProps) {
        super(props);

        this.onLoad = this.onLoad.bind(this);
    }

    onLoad(): void {
        const size = document.body.clientWidth < 600 ? "compact" : "normal";

        window.grecaptcha.render("g-recaptcha", {
            sitekey: "6LfdGcEUAAAAAOEl8TJPkBLwXzwL4JFWdV4z2vmU",
            theme: "light",
            size: size,
            callback: this.props.onValidated
        });
    }

    componentDidMount(): void {
        window.captchaOnLoad = this.onLoad;

        const url = "https://www.google.com/recaptcha/api.js?onload=captchaOnLoad&render=explicit";
        const scripts = document.getElementsByTagName("script");
        for (var i = 0, length = scripts.length; i < length; ++i) {
            const script = scripts.item(i);
            if (script && script.src === url) {
                if (window.grecaptcha) {
                    this.onLoad();
                }

                return;
            }
        }
        
        const script = document.createElement("script");
        script.type = "text/javascript";
        script.async = true;
        script.src = url;      

        document.body.appendChild(script);
    }

    render(): React.ReactElement {
        return (
            <div id="g-recaptcha" data-sitekey="6LfdGcEUAAAAAOEl8TJPkBLwXzwL4JFWdV4z2vmU"></div>
        )
    }
}