import React from 'react';
import './ModernizeSlide.scss';
import {InternalLink} from "../framework/Links";
import {RouteID} from "../application/Routes";

export default class ModernizeSlide extends React.PureComponent {
  render() {
    return (
        <section id="modernize-slide">
            <h1>
                WE MODERNIZE
            </h1>
            <p>
                Technology evolves rapidly. What have been working for years may no longer reflect your customers needs.
            </p>
            <p>
                While staying in touch with reality Ultima Labs team stays in touch with the modern trends and technology as well.
                Should it be a facelift for you platform or more comprehensive upgrade - we are here to help.
            </p>
            <p>
                <InternalLink routeID={RouteID.ROUTE_ID_MODERNIZE}>READ THE STORY</InternalLink>
            </p>
            <div>
                <InternalLink routeID={RouteID.ROUTE_ID_CONTACT} type="primary accent button" />
            </div>
        </section>
    );
  }
}