import React from 'react';
import site from '../site.svg'
import './SVG.scss';

export type SVGProps = {
    icon: 'location'
         |'phone'
         |'twitter'
         |'linkedin'
         |'facebook'
         |'instagram'
         |'github'
         |'vk'
         |'envelope'
         |'profile';
}

export default class SVG extends React.PureComponent<SVGProps> {
  render() {
    return (
      <svg className={`icon icon-${this.props.icon}`}>
        <use xlinkHref={`${site}#icn-${this.props.icon}`} />
      </svg>
    );
  }
}
